import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { MEDIA_REPLACE_URL } from '../../../../Services/Utils/utils'
import ProfileImage from '../../CommonComponents/ProfileImage/ProfileImage';

class ProfileOverview extends Component {

    render() {
        const { profile, userData } = this.props
        return userData && Object.keys(userData).length > 0 ? <div className="container container-xs">
            <div className="mt-xxl-3 mt-2">
                <h3 className="fs-6 text-gray-800">Profile Overview</h3>
            </div>
            <div className="card mb-3 mb-xxl-4">
                <div className="card-body p-3 d-flex justify-content-between align-items-start flex-wrap">
                    <div className="d-flex align-items-center flex-wrap flex-grow-1">
                        <div className="me-3 me-xxl-4">
                            <ProfileImage className={'symbol h-90px w-90px position-relative rounded-circle overflow-hidden bg-light'}
                                imageSize={90}
                                symbolClass={'symbol-label text-light-alt fw-bold display-6 text-uppercase'}
                                name={userData.name} profileUrl={MEDIA_REPLACE_URL(userData.picture)} networkId={0} />
                        </div>
                        <div className="d-flex flex-column my-2">
                            <div className="d-flex align-items-center mb-2">
                                <h6 className="fs-4 text-dark text-hover-primary mb-0 me-1">{userData.name}</h6>
                                {userData.access == 1 ? <span className="svg-icon svg-icon-1 svg-icon-primary">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24">
                                        <path d="M10.0813 3.7242C10.8849 2.16438 13.1151 2.16438 13.9187 3.7242V3.7242C14.4016 4.66147 15.4909 5.1127 16.4951 4.79139V4.79139C18.1663 4.25668 19.7433 5.83365 19.2086 7.50485V7.50485C18.8873 8.50905 19.3385 9.59842 20.2758 10.0813V10.0813C21.8356 10.8849 21.8356 13.1151 20.2758 13.9187V13.9187C19.3385 14.4016 18.8873 15.491 19.2086 16.4951V16.4951C19.7433 18.1663 18.1663 19.7433 16.4951 19.2086V19.2086C15.491 18.8873 14.4016 19.3385 13.9187 20.2758V20.2758C13.1151 21.8356 10.8849 21.8356 10.0813 20.2758V20.2758C9.59842 19.3385 8.50905 18.8873 7.50485 19.2086V19.2086C5.83365 19.7433 4.25668 18.1663 4.79139 16.4951V16.4951C5.1127 15.491 4.66147 14.4016 3.7242 13.9187V13.9187C2.16438 13.1151 2.16438 10.8849 3.7242 10.0813V10.0813C4.66147 9.59842 5.1127 8.50905 4.79139 7.50485V7.50485C4.25668 5.83365 5.83365 4.25668 7.50485 4.79139V4.79139C8.50905 5.1127 9.59842 4.66147 10.0813 3.7242V3.7242Z" fill="#6ac259"></path>
                                        <path d="M14.8563 9.1903C15.0606 8.94984 15.3771 8.9385 15.6175 9.14289C15.858 9.34728 15.8229 9.66433 15.6185 9.9048L11.863 14.6558C11.6554 14.9001 11.2876 14.9258 11.048 14.7128L8.47656 12.4271C8.24068 12.2174 8.21944 11.8563 8.42911 11.6204C8.63877 11.3845 8.99996 11.3633 9.23583 11.5729L11.3706 13.4705L14.8563 9.1903Z" fill="#fff"></path>
                                    </svg>
                                </span> : null}
                            </div>
                            <div className="d-flex flex-wrap pe-2">
                                <p className="d-flex align-items-center text-decoration-none text-hover-primary mb-2 fw-medium">
                                    <span className="svg-icon svg-icon-4 me-1 text-dark">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M3 3H21C21.5523 3 22 3.44772 22 4V20C22 20.5523 21.5523 21 21 21H3C2.44772 21 2 20.5523 2 20V4C2 3.44772 2.44772 3 3 3ZM20 7.23792L12.0718 14.338L4 7.21594V19H20V7.23792ZM4.51146 5L12.0619 11.662L19.501 5H4.51146Z" fill="currentColor"></path></svg>
                                    </span>
                                    {userData.emailId}
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="d-flex mt-4">
                        {profile === 'view' ?
                            <Link to={'/profile/edit'} className="btn btn-primary btn-sm me-2">Edit Profile</Link> :
                            <Link to={'/profile'} className="btn btn-primary btn-sm me-2" >View Profile</Link>}
                    </div>
                </div>
            </div>
        </div> : null

    }
}

export default ProfileOverview;